.hero-section-form {
    display: flex;
    flex-direction: column;
}

.hero-section-form__field {
    display: flex;
    align-items: center;
    height: 46px;

    @media screen and (min-width: 1170px) {
        height: 50px;
        width: 415px;
    }
}

.hero-section-form__field:not(:last-of-type) {
    margin-bottom: 20px;
}

.hero-section-form__input {
    width: 100%;
    height: 100%;
    padding: 0 20px;
    border: transparent;
    background-color: #F5F5F5;
}

.hero-section-form__field:nth-child(1) .hero-section-form__input {
    border-radius: 30px;
}

.hero-section-form__field:nth-child(2) .hero-section-form__input {
    border-radius: 30px 0px 0px 30px;
}

.hero-section-form__input,
.hero-section-form__input::placeholder {
    color: #2E2E2E;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    @media screen and (min-width: 1170px) {
        font-size: 18px;
    }
}

.hero-section-form__button {
    color: #FFF;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    
    height: 100%;
    padding: 0 20px;
    border-radius: 0 30px 30px 0;
    background-color: #4768BD;
    border: transparent;

    @media screen and (min-width: 1170px) {
        font-size: 18px;
        padding: 0 30px;
    }
}

.hero-section-form__request {
    color: #979797;
    text-align: center;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 132.023%;
    margin-top: 20px;

    @media screen and (min-width: 1170px) {
        text-align: start;
        font-size: 20px;
        line-height: normal;
        margin-top: 40px;
        width: 595px;
    }
}

.hero-section-form__request--mobile {
    display: block;

    @media screen and (min-width: 1170px) {
        display: none;
    }
}

.hero-section-form__request--desktop {
    display: none;

    @media screen and (min-width: 1170px) {
        display: block;
    }
}
