// menu-menu настройки скрола 
// когда открыто мобильно меню body скролить нельзя меню можно
.menu-container {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    
    background-color: rgba(0, 0, 0, 0.50);
    z-index: 999;

    overflow: hidden;
    overflow-y: auto;

    transform: translateX(100%);
    transition: transform 250ms ease-in-out;

    .menu-container-wrap {
        width: 100%;
        min-height: 100%;
        margin: auto;
    }
}

.menu-container.is-open {
    transform: translateX(0);
}

.modal-is-open {
    overflow: hidden;
}
