.earned-section {
    display: none;
    
    @media screen and (min-width: 1170px) {
        display: block;
        background-color: #F5F5F5;
    }
}

.earned-section-container {
        display: flex;
        justify-content: space-between;
        padding-top: 20px;
        padding-bottom: 20px;
}

.earned-section__item {
    color: #2E2E2E;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    span {
        color: #56BE54;
        font-weight: 600;
    }
}
