body {
    // mobile
    background-image: url(../images/backgraund-body-img-phone-mobile.png), url(../images/backgraund-body-img-top-mobile.jpg);
    background-repeat: no-repeat, no-repeat;
    background-position: left 50% top 165px, center top 97px;
    background-size: contain, contain;

    @media (min-device-pixel-ratio: 2),
    (min-resolution: 192dpi),
    (min-resolution: 2dppx) {
        background-image: url(../images/backgraund-body-img-phone-mobile.png), url(../images/backgraund-body-img-top-mobile.jpg);
    }

    // tablet
    @media screen and (min-width: 350px) {
        background-image: url(../images/backgraund-body-img-phone-tablet.png), url(../images/backgraund-body-img-top-mobile.jpg);
        background-repeat: no-repeat, no-repeat;
        background-position: right 50% top 208px, center top 97px;
        background-size: 396px, contain;
    
        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-tablet.png), url(../images/backgraund-body-img-top-mobile.jpg);
        }
    }

    @media screen and (min-width: 350px) {
        background-image: url(../images/backgraund-body-img-phone-tablet.png),
        url(../images/backgraund-body-img-top-desktop.jpg);
        background-repeat: no-repeat,
        no-repeat;
        background-position: right 50% top 208px,
        center top 97px;
        background-size: 396px,
        1963px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-tablet.png),
            url(../images/backgraund-body-img-top-desktop.jpg);
        }
    }

    // desktop
    @media screen and (min-width: 1170px) {
        background-image: url(../images/backgraund-body-img-phone-desktop.png),
        url(../images/backgraund-body-img-top-desktop.jpg);
        background-position: left 278px top 116px, center top 113px;
        background-size: 917.68px,
        1963px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-desktop.png),
                    url(../images/backgraund-body-img-top-desktop.jpg);
        }
    }

    @media screen and (min-width: 1270px) {
        background-image: url(../images/backgraund-body-img-phone-desktop.png),
        url(../images/backgraund-body-img-top-desktop.jpg);
        background-position: left 79% top 116px, center top 113px;
        background-size: 917.68px,
        1963px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-desktop.png),
                    url(../images/backgraund-body-img-top-desktop.jpg);
        }
    }

    @media screen and (min-width: 1770px) {
        background-image: url(../images/backgraund-body-img-phone-desktop.png),
        url(../images/backgraund-body-img-top-desktop.jpg);
        background-position: left 675px top 116px,
        center top 113px;
        background-size: 917.68px,
        1963px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-desktop.png),
            url(../images/backgraund-body-img-top-desktop.jpg);
        }
    }

    @media screen and (min-width: 2070px) {
        background-image: url(../images/backgraund-body-img-phone-desktop.png),
        url(../images/backgraund-body-img-top-desktop.jpg);
        background-position: left 59% top 116px,
        center top 113px;
        background-size: 917.68px,
        1963px;

        @media (min-device-pixel-ratio: 2),
        (min-resolution: 192dpi),
        (min-resolution: 2dppx) {
            background-image: url(../images/backgraund-body-img-phone-desktop.png),
            url(../images/backgraund-body-img-top-desktop.jpg);
        }
    }
}