.header{
    padding-top: 20px;
    padding-bottom: 28px;

    @media screen and (min-width: 1170px) {
        padding-top: 39px;
        padding-bottom: 41px;
    }
}

.header__container {
    display: flex;
    align-items: center;
}

.header__logo-desktop {
    display: none;

    @media screen and (min-width: 1170px) {
        display: flex;
        align-items: center;
    }
}

.header__logo-mobile {
    display: flex;
    align-items: center;
    
    @media screen and (min-width: 1170px) {
        display: none;
    }
}

.header__nav,
.header__language-switch,
.header__auth {
    display: none;

    @media screen and (min-width: 1170px) {
        display: block;
    }
} 

.header__nav {
    @media screen and (min-width: 1170px) {
        margin-left: 50px;

        .header__nav-list {
            display: flex;
        }
    
        .header__nav-item:not(:last-child) {
            margin-right: 50px;
        }

        .header__nav-link {
            color: #2E2E2E;
            font-family: Inter;
            font-size: 18px;
            font-style: normal;
            font-weight: 500;
            line-height: normal;
            text-decoration: none;
        }
    }
}

.header__language-switch,
.header__language-switch-mobile {
    select {
        border: none;
    }
}

.header__language-switch {
    @media screen and (min-width: 1170px) {
           margin-left: 50px;
    }
}

.header__auth {
    @media screen and (min-width: 1170px) {
        display: flex;
        margin-left: auto;
    }
}

.header__register {
    @media screen and (min-width: 1170px) {
        margin-right: 20px;
    }
}

.header__register-link,
.header__login-link {
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    height: 46px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    border-radius: 30px;

    @media screen and (min-width: 1170px) {
        font-size: 18px;
        height: 50px;
    }
}

.header__register-link {
    color: #4768BD;
    width: 155px;
    border: 1px solid #4768BD;
}

.header__login-link {
    width: 87px;
    color: #FFF;
    background: #4768BD;
    @media screen and (min-width: 1170px) {
        width: 113px;
    }
}

.menu-toggle {
    min-height: 30px;
    min-width: 30px;
    display: flex;
    align-items: center;
    justify-content: center;

    margin: 0;
    margin-left: auto;
    padding: 0;
    background-color: transparent;
    cursor: pointer;
    border: transparent;
    outline: none;

    @media screen and (min-width: 1170px) {
        display: none;
    }
}

// menu-menu стили контента
.header-navigation-block {
    background-color: #FFF;
}

.header-navigation-block-container {
    padding-top: 20px;
    padding-bottom: 40px;
}

.brand-Language-Menu {
    display: flex;
    align-items: center;
    padding-bottom: 40px;
}

.brand-Language-Menu .menu-toggle {
    margin: 0;
}

.header__language-switch-mobile {
    margin-left: auto;
    margin-right: 20px;
}

.header__nav-mobile {
    margin-bottom: 72px;
}

.header__nav-item-mobile:not(:last-child) {
    margin-bottom: 20px;
}

.header__nav-link-mobile {
    color: #2E2E2E;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    text-decoration: none;
}

.header__auth-mobile {
    display: flex;
    align-items: center;
    flex-wrap: wrap;

    .header__login {
        margin-right: 18px;
        margin-bottom: 15px;

        @media screen and (min-width: 290px) {
            margin-bottom: 0;
        }
    }
}
