/* placeholders */
%body {
  font-family: 'Inter', sans-serif, 'Poppins', sans-serif;
  font-size: 14px;
  background-color: #FEFEFF;
}

body {
  @extend %body;
}

// body.modal-open {
//   overflow: hidden;
// }

%img {
  display: block;
  width: 100%;
  height: auto;
}

img {
  @extend %img;
}

%tag {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  @extend %tag;
}

%list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.header__nav-list,
.header__nav-list-mobile {
  @extend %list;
}
